* {
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  width: 500px;
  border: 2px solid black;
  height: max-content;
}
body {
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  text-align: center;
}

.title {
  color: crimson;
}
.title h1 {
  padding-top: 10px;
  text-shadow: 2px 2px 2px black;
}
.input-form {
  width: 90%;
}
.form-item {
  padding-left: 10px;
  padding-top: 4px;
}
.form-item label {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-bottom: 5px;
}
.form-item input {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  width: 100%;
}
.btn-submit {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 10px;
  margin-right: 230px;
}
.form-submit {
  display: flex;
  justify-content: flex-start;
  gap: 8px;
}
.form-submit button:nth-child(1) {
  padding: 10px;
  background-color: rgb(149, 149, 255);
  border: none;
  border-radius: 4px;
}
.form-submit button:nth-child(2) {
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: rgb(251, 96, 24);
  border: none;
}
button:hover {
  scale: 1.05;
}
.transaction-list {
  margin-top: 20px;
  margin-bottom: 15px;
  color: crimson;
  text-shadow: 1px 1px 1px black;
  text-align: center;
}

.transactions {
  display: flex;
  justify-content: space-between;
}
.income-heading,
.expense-heading {
  margin-bottom: 16px;
}
.income-item,
.expense-item {
  width: 100%;
}
.income-list,
.expense-list {
  width: 100%;
  padding: 16px;
}
.item-left {
  width: 100%;
  display: flex;
  text-align: left;
}
.card-item {
  width: 100%;
  margin-top: 6px;
}
.item-right {
  width: 100%;
  padding-top: 7px;
}
.item-right button {
  width: 70px;
  border-color: red;
  background-color: red;
  color: white;
}


